.developer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: @color-gray-700;
    font-size: 16px;
    text-decoration: none;

    &:hover {
        .developer__image {
            color: @color-primary-500;
        }
    }

    &:active {
        opacity: 0.3;


        .developer__image {
            color: @color-primary-500;
        }
    }
}
