.contacts {
    @media (min-width: @breakpoint-large) {
        position: relative;
        z-index: 0;
        min-height: 400;
        padding: 95px 0 107px;
        display: grid;
        grid-template-columns: 1220px;
        align-items: center;
        justify-content: center;
    }
}

.contacts__container {
    width: 280px;
    margin-right: auto;
    margin-left: auto;
    padding: 26px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;

    @media (min-width: @breakpoint-medium) {
        width: 708px;
        padding: 56px 40px;
        grid-template-columns: 172px 172px;
        justify-content: space-between;
    }

    @media (min-width: @breakpoint-large) {
        position: relative;
        z-index: 1;
        width: 565px;
        padding: 56px 80px;
        margin: 0;
        background-color: @color-white;
    }
}

.contacts__heading {
    margin: 0;
    padding-right: 10px;
    font-family: @font-heading;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;

    @media (min-width: @breakpoint-medium) {
        font-size: 20px;
        padding-right: 0;
        line-height: 26px;
    }
}

.contacts__address {
    margin: 0;
    color: @color-gray-700;
    line-height: 20px;

    @media (min-width: @breakpoint-medium) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: right;
    }
}

.contacts__map {
    display: block;
    width: 100%;
    height: 362px;
    border: none;

    @media (min-width: @breakpoint-medium) {
        height: 400px;
    }

    @media (min-width: @breakpoint-large) {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
}
